import React from "react";
import Header from "./component/navbar";
import Footer from "./component/footer";
import { Switch, Route } from "react-router-dom";
import Home from "./pages/Home";
import About from "./pages/About";
import Kursus from "./pages/Course";
import Blogs from "./pages/Blog";
import Detail from "./pages/DetailKelas";
import Kontak from "./pages/Kontak";
import Single from "./pages/ArtikelSingle";

function App() {
  return (
    <div>
      <Header />
      <Switch>
        <Route exact path="/" component={Home} />
        <Route path="/tentang" component={About} />
        <Route path="/kelas" component={Kursus} />
        <Route path="/detail" component={Detail} />
        <Route path="/artikel" component={Blogs} />
        <Route path="/single" component={Single} />
        <Route path="/Kontak" component={Kontak} />
      </Switch>
      <Footer />
    </div>
  );
}

export default App;
